/*container*/
.contactContainer {
    display: flex;
    background-color: transparent;
    position: flex-end;
    /*column organization*/
    flex-direction: column; 
    margin-top: 5%;
    margin-right: 5%;
    margin-left: 5%;
    margin-bottom: 5%;
}

/*title*/
.contactTitle {
    color: #ffffff;
    font-size: 80px;
    font-family: Dirt;
    animation: glowing 3s infinite;
}

/*list*/
.contactElementList {
    list-style: none;
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: center;
    
}

/*element container inside list*/
.contactElementContainer {
    display: flex;
    align-items: center;
    position: relative; /* Necesario para posicionar el ::before de forma absoluta */
    max-width: 60vmin;
}

/*element*/
.contactElement {
    background-color: #000000;
    border-radius: 10%;
    max-width: 20%;
    height: auto;
}

/*text*/
.contactText {
    color: #04d9ff;
    font-family: lato;
    font-size: 80px;
    margin-left: 5%;
    transition: color 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
}

.contactText:hover {
  box-shadow: inset 900px 0 0 0 #54b3d6;
  color: white;
}





@media (max-width: 1500px) {
    .contactTitle {
      font-size: 65px;     
    }
